body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.playerCon {
    display: flex;
    flex: 1 1; 
    height: 100vh;
    background-color: #000;
    align-items: center;
    justify-content: center
}
.player {
    width: 100vw; height: 100vh;
    background-color: #000;
    object-fit: contain
}
.conIcon {
    color: #fff; 
    font-size: 66px
}
.bufferIcon {
    -webkit-animation: App-logo-spin infinite 1.3s linear;
            animation: App-logo-spin infinite 1.3s linear;
    color: #fff; 
    font-size: 66px
}
.uxCon {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.1)
}
.controlsCon {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    background-color: rgba(0,0,0,0.1)
}
.topCon {
    flex:1 1;
    display: flex;
    flex-direction: row;
}
.topConIcon {
    color: #fff; 
    font-size: 56px
}
.topButton {
    height: 90px;
    padding:15px;
    padding-top: 0px;
    display: flex;
}
.backButton {
    height: 90px;
    background-color: transparent;
    border: none;
    opacity: 0.6;
    cursor: pointer;
}
.backButton:hover {
    opacity: 1
}
.backButton:active {
    opacity: 0.7
}
.middleCon {
    flex:1 1;
    position: relative;
}
.controlCon {
    flex:1 1;
    position: relative;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.playCon {
    width: 25%;
}
.playConIn {
    flex:1 1
}
.control {
    background-color: transparent;
    border: none;
    outline: none;
    opacity: 0.6;
    cursor: pointer;
    width: 25%;
}
.control:hover {
    opacity: 1
}
.control:active {
    opacity: 0.7
}

.bufferIconCon {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center
}

.bottomCon {
    flex:1 1;
    flex-direction: column;
    justify-content: flex-end;
}

.timeCon {
    width: 100%;
    display: flex;
    flex-direction:row;
    padding-left: 2%;
    padding-right: 2%;
    padding-bottom: 5px;
    padding-top: 5px;
    font-size: 19px
}

.time {
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold
}

.midTime {
    flex: 1 1
}


.sliderConDragger {
    position: relative;
    width: 100%;
    height: 30px;
    flex-direction: column;
    padding-left: 2%;
    padding-right: 2%;
    cursor: pointer;
    /* background-color: #fff */
}
.sliderCon {
    position: relative;
    width: 100%;
    height: 30px;
    flex-direction: column;
    justify-content: center;
}
.slider {
    width: 100%;
    position: relative;
    height: 6px;
    background-color: rgba(255,255,255,0.4);
}
.sliderIn {
    position: absolute;
    z-index: +3;
    top:0;
    left: 0;
    bottom: 0;
    background-color: rgb(0, 0, 251);
    background-image: linear-gradient(to right, blue 0%, rgb(222, 0, 165) 100%);
}

.bufferCon {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    justify-content: flex-start;
}
.bufferIn {
    position: absolute;
    top:0;
    bottom: 0;
    background-color: #fff;
}

.volSliderCon {
    width: 10%;
    padding-top: 9px;
    padding-bottom:9px;
    align-content: center;
    align-items: center
  }

  .sliderInpCon {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: +9
  }

  .seekSlider {
  -webkit-appearance: none;
  display: flex;
  width: 100%;
  border-radius: 5px;  
  background-color: transparent;
  outline: none;
  transition: opacity .2s;
  cursor: pointer;
}

.seekSlider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background-color: rgb(0, 0, 251);
  background-image: linear-gradient(to right, blue 0%, rgb(222, 0, 165) 100%);
  cursor: pointer;
}

.seekSlider::-moz-range-thumb {
  width: 21px;
  height: 21px;
  border-radius: 50%;
  background-color: rgb(0, 0, 251);
  background-image: linear-gradient(to right, blue 0%, rgb(222, 0, 165) 100%);
  cursor: pointer;
}

.sliderThumbCon {
    position: absolute;
    z-index: +5;
    width: 100%;
    height: 50px;
    flex-direction: column;
    justify-content: center;
    background-color: #fff
}
.sliderThumb {
    position: absolute;
    background-color: rgb(222, 0, 165);
    width: 15px;
    height: 15px;
    border-radius: 50%;
}

