.title {

    background: linear-gradient(to right, blue 0%, rgb(222, 0, 165) 100%);
    font-size: 39px;
  /* background: -webkit-linear-gradient(#eee, #333); */
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.titleCon {
    display: inline-block;
    max-width: 15vw;
    padding: 0px;
    background-color: #282c34;
    overflow: hidden;
    cursor: pointer;
    object-fit: scale-down;
    box-shadow: 0px 3px 23px 1px rgba(0,0,0,0.75);
}
.titleCon:hover {
    box-shadow: 0px 3px 81px 1px rgba(0,0,0,0.75);
}

.titlePic {
    display: block;
    width: 100%;
    z-index: +3;
}


